.header__section {
  background-color: $heroBgcColor;
  position: relative;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 48px;
  padding-bottom: 48px;
}

.header__navigation-list {
  display: none;
}

.header__btn {
  display: none;
}

.header__mobile-list {
  display: flex;
  align-items: center;
}

.header__mobile-item:not(:last-child) {
  margin-right: 20px;
}

.header__mobile-phone {
  width: 24px;
  height: 24px;
}

.header__mobile-burger {
  width: 16px;
  height: 16px;
}

.header__mobile-menu {
  border: none;
  background-color: transparent;
  text-align: center;
}

.header__navigation-link {
  text-decoration: none;
  padding-top: 20px;
  padding-bottom: 20px;
  color: $whiteColor;
  transition: color 250ms $bezier;

  &:hover,
  &:focus {
    color: $accentColor;
  }
}

.active {
  font-weight: 900;
  color: $accentColor;
}

.header__menu-container {
  display: none;
  position: absolute;
  width: 100%;
  top: 100%;
  left: 0;
  padding: 60px 20px;

  background-color: $heroBgcColor;
  text-align: center;
}

.header__menu-item:not(:last-child) {
  margin-bottom: 40px;
}

.is-open {
  display: block;
}

@media screen and (min-width: 1440px) {
  .header__wrapper {
    display: block;
    padding-top: 24px;
    padding-bottom: 24px;
  }

  .header__mobile-list {
    display: none;
  }

  .header__navigation-list {
    display: flex;
    align-items: center;
  }

  .header__btn {
    display: block;
  }

  .header__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header__navigation-item:not(:last-child) {
    margin-right: 36px;
  }

  .header__btn {
    width: 156px;
    height: 48px;

    background-color: rgba(20, 20, 26, 0.3);
    border: 2px solid $accentColor;
    box-sizing: border-box;
    border-radius: 383px;
    color: $whiteColor;
  }
}
